*::after {
  box-sizing: inherit; }

body {
  min-width: 1170px;
  margin: 0;
  box-sizing: border-box;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #5e606a;
  font-weight: normal;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-image: url(../../img/picture-bg2.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-size: cover; }

.page-header__wrapper {
  margin: 0 auto;
  width: 1170px;
  padding: 0 15px; }

.page-header__top {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-top: 67px;
  padding-bottom: 20px; }

.page-header__call {
  margin-left: 65px; }

.page-header__call a {
  color: #ffffff;
  text-transform: uppercase; }

.page-header__call-phone {
  font-size: 16px;
  line-height: 16px; }

.page-header__call-button {
  font-size: 11px;
  line-height: 11px;
  padding: 7px 34px;
  margin-left: 30px;
  border-width: 2px;
  border-style: solid;
  border-color: #ffffff;
  border-radius: 17px; }

.page-header__middle {
  background-image: url(../../img/picture-bg1.png);
  background-repeat: no-repeat;
  background-position: -22% 40%;
  padding-top: 111px;
  padding-bottom: 140px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

.btn {
  border: 1px solid transparent;
  vertical-align: middle; }

.btn-rose {
  background-color: #fb0275;
  display: inline-block;
  color: #ffffff;
  font-size: 16px;
  line-height: 16px;
  font-weight: normal;
  text-transform: uppercase;
  border-radius: 50px; }

.main-nav {
  margin-left: 66px; }

.main-nav__list {
  width: 555px;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  list-style: none; }

.main-nav__link {
  display: inline-block;
  padding: 4px 25px;
  color: #ffffff;
  font-size: 13px;
  line-height: 15px;
  text-transform: uppercase; }

.header-info {
  width: 514px; }

.header-info__slogan {
  width: 332px;
  font-family: 'Montserrat', sans-serif;
  font-size: 60px;
  line-height: 74px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0; }

.header-info__title {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  line-height: 32px;
  font-weight: normal;
  color: #ffffff; }

.header-info__request {
  width: 485px;
  margin-top: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap; }

.header-info__link {
  padding: 17px 42px; }

.header-info__delivery {
  width: 210px;
  margin: 0;
  margin-top: 10px;
  color: #ffffff;
  font-size: 16px;
  line-height: 16px;
  font-weight: normal; }

.section-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 30px;
  line-height: 30px;
  font-weight: bold;
  color: #7f38bd; }

.main__container {
  border: 1px solid red; }

.main__wrapper {
  margin: 0 auto;
  width: 1170px;
  padding: 0 15px;
  border: 1px solid green; }

.about {
  padding-top: 103px;
  padding-bottom: 145px; }

.about__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between; }

.about__left {
  position: relative; }

.about__image {
  margin-left: 20px; }

.about__list {
  list-style: none; }

.about__item {
  font-size: 13px;
  line-height: 18px;
  text-align: center; }

.about__item:first-child {
  width: 145px;
  position: absolute;
  top: 110px;
  left: 170px; }

.about__item:nth-child(2) {
  width: 190px;
  position: absolute;
  top: 250px;
  right: -35px; }

.about__item:nth-child(3) {
  width: 173px;
  position: absolute;
  top: 290px;
  left: -10px; }

.about__item:last-child {
  width: 130px;
  position: absolute;
  bottom: -27px;
  left: 215px; }

.about__right {
  width: 590px;
  padding-left: 50px; }

.about__slogan {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  line-height: 32px;
  font-weight: normal;
  color: #383d52; }

.about__slogan {
  width: 385px; }

.about__text {
  width: 523px; }

.services__item--one {
  background-color: #faf8fb;
  padding-top: 100px;
  padding-bottom: 117px; }

.services__container {
  display: flex; }

.services__slogan {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  line-height: 32px;
  font-weight: normal;
  color: #383d52; }

.services__slogan--one {
  width: 450px;
  margin-bottom: 60px; }

.banners-print__info {
  width: 488px; }

.banners-print__list {
  margin-bottom: 75px; }

.banners-print__consultation {
  width: 473px;
  background-color: #883eca;
  border-radius: 10px;
  box-shadow: 0px 10px 23px -10px #510856;
  padding: 33px 34px;
  padding-bottom: 37px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around; }

.banners-print__consultation-left {
  margin-right: 23px; }

.banners-print__consultation-left span {
  font-size: 13px;
  line-height: 24px;
  font-weight: normal;
  font-style: italic;
  color: #ffffff; }

.banners-print__consultation-left p {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
  font-weight: bold;
  color: #ffffff; }

.banners-print__consultation-link {
  padding: 15px 45px; }

.banners-print__slider {
  width: 585px;
  margin: 0;
  padding: 0;
  padding-top: 81px; }

.slider {
  margin: 0;
  padding: 0;
  position: relative; }

.slider__content {
  width: 652px;
  height: 545px; }

.slider__controls::before,
.slider__controls::after {
  position: absolute;
  content: "";
  bottom: 15px;
  width: 90px;
  height: 14px;
  background-repeat: no-repeat;
  background-position: 0 0; }

.slider__controls::before {
  background-image: url(../img/arrow-left.png);
  left: 47px; }

.slider__controls::after {
  background-image: url(../img/arrow-right.png);
  left: 170px; }

.slider__comment {
  position: absolute;
  bottom: 12px;
  right: 130px; }
